.loader-wrapper {
  width: 100vw;
  height: 100vh;
  z-index: 99999999;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.loader-wrapper.hide-wrapper {
  opacity: 0;
  transition: opacity .5s;
}

.loader-wrapper.buried-wrapper {
  z-index: -10000000;
}

.loader {
  width: 30px;
  height: 30px;
  border: 4px solid #000;
  animation: loader 2s infinite;
  display: inline-block;
  position: relative;
}

.loader-inner {
  vertical-align: top;
  width: 100%;
  background-color: #fff;
  animation: loader-inner 2s ease-in infinite;
  display: inline-block;
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(180deg);
  }

  50% {
    transform: rotate(180deg);
  }

  75% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes loader-inner {
  0% {
    height: 0%;
  }

  25% {
    height: 0%;
  }

  50% {
    height: 100%;
  }

  75% {
    height: 100%;
  }

  100% {
    height: 0%;
  }
}

.image-loader-wrapper {
  height: 100%;
  z-index: 1;
  opacity: 1;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  margin: 0;
  transition: all .25s;
  display: inline-flex;
  position: absolute;
  left: 50%;
}

.image-loader {
  width: 30px;
  height: 30px;
  z-index: 1;
  border: 4px solid #000;
  animation: loader 2s infinite;
  display: inline-block;
  position: fixed;
}

.image-loader-inner {
  vertical-align: top;
  width: 100%;
  z-index: 1;
  background-color: #fff;
  animation: loader-inner 2s ease-in infinite;
  display: inline-block;
}

